import React, { useEffect } from "react";
import { Qna } from "../../Model/db";
import { RootState } from "../../Redux/index";
import { useSelector, useDispatch } from "react-redux";
import styled from "styled-components";
import { useState } from "react";
import { RouteComponentProps } from "react-router-dom";
import { getPrdQueList, getProductDetail } from "../../Redux/Product/thunk";
import { getAllQuestion } from "../../Redux/Admin/thunk";

const QuestionItem = styled.div`
  display: flex;
  margin-top: 5px;
  align-items: center;
  :first-child {
    margin-top: 15px;
  }
  p {
    padding: 0 auto;
    width: 10%;
    text-align: center;
    :nth-child(2) {
      cursor: pointer;
      margin-left: 10%;
      width: 70%;
      text-align: left;
    }
  }
`;
const AnswerItem = styled.div`
  margin-top: 5px;
  padding: 10px;
  background-color: lightgray;
  p {
    margin-left: 5%;
  }
`;

export default function QnaLine({
  page,
  qna,
  pid,
}: {
  page: string;
  qna: Qna;
  pid: string;
}) {
  const dispatch = useDispatch();

  const [popupClosed, setpopupClosed] = useState(false);
  useEffect(() => {
    if (popupClosed) {
      if (page === "admin") {
        dispatch(getAllQuestion());
      } else {
        dispatch(getProductDetail(pid));
      }
    }
  }, [popupClosed]);
  // popup 
  const [visibleAnswer, setvisibleAnswer] = useState(false);
  const user = useSelector((state: RootState) => state.user.user);
  const isAdmin = user ? user.isAdmin : false;
  let newUserId = qna.user.userId.slice(0, 4);
  newUserId += "****";
  const clickAddAnswer = () => {
    const popup = window.open(
      `/answerform/${qna.id}`,
      "PopupWin",
      "width=500,height=650"
    );
    const interval = setInterval(() => {
      if (popup?.closed) {
        setpopupClosed(true);
        clearInterval(interval);
      }
    }, 1000);
  }; //admin 
  //     1 
  //      

  const clickContent = () => {
    setvisibleAnswer(!visibleAnswer);
  }; //   
  return (
    <>
      <QuestionItem>
        {isAdmin ? (
          <p>
            {qna.answer ? (
              ""
            ) : (
              <button id={qna.id} onClick={clickAddAnswer}>
                
              </button>
            )}
          </p>
        ) : (
          <p>{qna.answer ? "" : ""}</p>
        )}
        <p onClick={clickContent}>{qna.content}</p>
        <p>{newUserId}</p>
        <p>{qna.createdAt.slice(0, 10)}</p>
      </QuestionItem>
      {qna.answer && visibleAnswer && (
        <AnswerItem>
          <p>{qna.answer.content}</p>
        </AnswerItem>
      )}
    </>
  );
}
