// global-style.ts
import { createGlobalStyle } from "styled-components";
import { reset } from "styled-reset";

//  import   ~!
export const GlobalStyle = createGlobalStyle`
  ${reset};
  * {
        box-sizing:border-box;
    }
  body{
    background-color:${(props) => props.theme.colors.main};
  }
  /*      */
`;
