import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import styled from "styled-components";
import {
  addProduct,
  AddProductData,
  addImage,
  editProduct,
} from "../Redux/Product/thunk";
import { useInputNum, useInputStr } from "../Hooks/useInput";
import { Product } from "../Model/db";
import { RouteComponentProps } from "react-router-dom";
import { RootState } from "../Redux/index";
import { toast } from "react-toastify";

const Container = styled.div`
  margin-top: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
`;
const Column = styled.div`
  display: flex;
  align-items: center;
  width: 100%;
  &:not(:last-child) {
    margin-bottom: 7px;
  }
  input {
    height: 30px;
    width: 70%;
    min-width: 100px;
  }
  div {
    width: 120px;
  }
`;
const Form = styled.div`
  padding: 40px;
  padding-bottom: 30px;
  margin-bottom: 15px;
  width: 50vw;

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    button {
      margin-top: 10px;
    }
  }
`;

interface Props extends RouteComponentProps {
  product: Product | undefined;
}

export default function AddProduct(props: Props) {
  const dispatch = useDispatch();

  const { product } = props;
  const [name, setName] = useState("");
  const onChangeName = (e: React.ChangeEvent<HTMLInputElement>) => {
    const elem = e.target as HTMLInputElement;
    setName(elem.value);
  };
  const {
    value: price,
    setValue: setPrice,
    onChange: onChangePrice,
  } = useInputNum(""); //   input
  const {
    value: stock,
    setValue: setStock,
    onChange: onChangeStock,
  } = useInputNum("");
  const {
    value: description,
    setValue: setDescription,
    onChange: onChangeDescription,
  } = useInputStr("");

  const [imgUrls, setimgUrls] = useState<Array<{ url: string; idx: string }>>([
    { url: "", idx: "" }, //
    { url: "", idx: "" }, //
  ]);
  const imgList = useSelector((state: RootState) => state.product.imagesPath);

  useEffect(() => {
    //product  undifined     
    //product   .
    // 
    if (product) {
      setName(product.name);
      setPrice(product.price.toString());
      setStock(product.stock.toString());
      setDescription(product.description);
    }
  }, []);

  const onThumbnail = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const image = e.target.files[0];
      const formData = new FormData();
      formData.append("image", image);
      dispatch(addImage(formData, "0"));
    }
  };
  //   s3  .
  // redux product    
  //useSelector    
  // useEffect   state imgUrls  .
  //form  submit   
  useEffect(() => {
    const newArr = [...imgUrls];
    imgList.forEach((img) => {
      newArr[Number(img.idx)] = img;
    });
    setimgUrls(newArr);
  }, [imgList]);

  const addImgUrl = () => {
    setimgUrls([...imgUrls, { url: "", idx: "" }]);
  }; //   input  .
  const onChangeUrl = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const image = e.target.files[0];
      const formData = new FormData();
      formData.append("image", image);
      dispatch(addImage(formData, e.target.id));
    }
  }; //   s3  .

  const submitForm = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (!product) {
      for (let i = 1; i < imgUrls.length; ++i) {
        if (imgUrls[i].url === "") {
          toast.error(" ");
          return;
        }
      }
    }

    const productForm: AddProductData = {
      name,
      price: Number(price),
      stock: Number(stock),
      imgUrls,
      description,
    };
    if (product) {
      await dispatch(
        editProduct({
          id: product.id,
          name,
          price: Number(price),
          stock: Number(stock),
        })
      );
    } else {
      await dispatch(addProduct(productForm));
    }
    props.history.push("/");
  };
  return (
    <>
      <Container>
        ADD PRODUCT
        <Form>
          <form encType="multipart/form-data" onSubmit={submitForm}>
            <div>
              <Column>
                <div>: </div>
                <input
                  id="name"
                  value={name}
                  onChange={onChangeName}
                  placeholder="Enter Product Name"
                  required={true}
                ></input>
              </Column>
              <Column>
                <div>  : </div>
                <input
                  id="price"
                  value={price}
                  onChange={onChangePrice}
                  placeholder="Enter Price"
                  required={true}
                ></input>
              </Column>

              <Column>
                <div>  : </div>

                <input
                  id="stock"
                  value={stock}
                  onChange={onChangeStock}
                  placeholder="Enter stock"
                  required={true}
                ></input>
              </Column>

              <Column>
                <div>  : </div>

                <input
                  id="description"
                  value={description}
                  onChange={onChangeDescription}
                  placeholder="Enter description"
                ></input>
              </Column>

              {!product && (
                <>
                  {imgUrls.map((imgUrl, i) => {
                    if (i == 0) {
                      return;
                    }
                    return (
                      <Column key={i}>
                        <div> {`${i}`}: </div>
                        <input
                          type="file"
                          id={i.toString()}
                          //  value={imgUrl.url}
                          onChange={onChangeUrl}
                          placeholder={`Enter imageUrl ${i}`}
                          required={true}
                          accept="image/*"
                        ></input>
                        {i == 1 && <p onClick={addImgUrl}></p>}
                      </Column>
                    );
                  })}

                  <Column>
                    <div> : </div>

                    <input
                      type="file"
                      id="thumnail"
                      onChange={onThumbnail}
                      placeholder="Enter thumnail"
                      required={false}
                      accept="image/*"
                    ></input>
                  </Column>
                </>
              )}
              <button>submit</button>
            </div>
          </form>
        </Form>
      </Container>
    </>
  );
}
