import React, { useEffect } from "react";
import styled from "styled-components";

import { RootState } from "../Redux/index";
import { useSelector, useDispatch } from "react-redux";

import { getOrder } from "../Redux/User/thunk";

const Wrapper = styled.div``;

const Container = styled.div``;
const Top = styled.div`
  display: flex;
  div {
    color: lightsalmon;
    font-size: 2em;
    font-weight: 500;
    margin-right: 1em;
  }
  margin: 10px 0;
`;
const TopProductCard = styled.div`
  display: grid;
  grid-template-columns: 100px 300px 100px 100px;
  border-top: 3px solid black;
  height: 2em;
  padding-top: 0.5em;
  margin-bottom: 20px;
`;

const Column = styled.div`
  text-align: center;
`;
const OrderCard = styled.div`
  margin-top: 1.5em;
  border: 1px solid lightgray;
`;
const ProductCard = styled.div`
  margin-bottom: 1em;

  display: grid;
  grid-template-columns: 100px 300px 100px 100px;
`;
const ProductCardColumn = styled.div`
  display: flex;
  justify-content: center;
`;

const ShipmentCard = styled.div`
  section {
    display: flex;

    label {
      width: 100px;
      background-color: lightgray;
      text-align: center;
      margin-top: 5px;
    }
    p {
      margin-left: 10px;
      margin-top: 5px;
    }
  }
`;

const Strong = styled.div`
  font-weight: 700;
  font-size: 1.5em;
`;
const ProductInfo = styled.div`
  display: flex;
`;

const ProductDescription = styled.div``;
const Image = styled.div`
  width: 100%;
`;
const Button = styled.button`
  height: 1.5em;
`;
export default function OrderList() {
  const orders = useSelector((state: RootState) => state.user.orders);
  const user = useSelector((state: RootState) => state.user.user);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getOrder());
  }, []);
  return (
    <Wrapper>
      <Container>
        {orders.map((order) => (
          <OrderCard id={order.id} key={order.id}>
            <Top>
              <div>
                <span> : </span>
                <span>{order.createdAt.slice(0, 10)}</span>
              </div>
              {user?.isAdmin && (
                <div>
                  <span> : </span>
                  <span>{order.user?.userId}</span>
                </div>
              )}
            </Top>
            <TopProductCard>
              <Column></Column>
              <Column></Column>
              <Column>
                <div></div>
                <div>()</div>
              </Column>
              <Column></Column>
            </TopProductCard>
            {order.products.map((product) => (
              <ProductCard key={product.id}>
                <ProductCardColumn>
                  <div>{order.orderNumber}</div>
                </ProductCardColumn>
                <ProductCardColumn>
                  <ProductInfo>
                    <Image>
                      <img
                        width={150}
                        height={150}
                        src={product.thumbnail}
                      ></img>
                    </Image>
                    <ProductDescription>
                      <Strong>{product.name}</Strong>
                      <p>{product.description}</p>
                    </ProductDescription>
                  </ProductInfo>
                </ProductCardColumn>
                <ProductCardColumn>
                  <div>
                    <div>{product.price} </div>
                    <div>{`(${product.count})`} </div>
                  </div>
                </ProductCardColumn>
                <ProductCardColumn>
                  <div></div>
                </ProductCardColumn>
              </ProductCard>
            ))}
            <ShipmentCard>
              <Strong> </Strong>
              <section>
                <label></label>
                <p>{order.name}</p>
              </section>
              <section>
                <label></label>
                <p>{order.phone}</p>
              </section>
              <section>
                <label></label>
                <p>{order.zipcode}</p>
                <p>{order.address1}</p>
                <p>{order.address2}</p>
              </section>
              <section>
                <label> </label>
                <p>{order.message}</p>
              </section>
            </ShipmentCard>
          </OrderCard>
        ))}
      </Container>
    </Wrapper>
  );
}
